<template>
	 <div id="myProfit" :style="{backgroundImage:'url('+bg+')'}">
     <!--用户信息-->
     <div class="homeHeader">
       <div class="headerLeft">
         <img :src="QR" alt="" class="userImg">
       </div>
       <div class="headerCenter">
         <div class="userTitle"><span>小布</span><span>{{id}}</span></div>
         <div class="userMoney">统计月份：{{yuefen}}</div>
         <div class="userMoney">收益小计：{{money}}</div>
       </div>
     </div>
     <ul class="tabTilte">
       <li @click="activeTabCont=0" :class="{active:activeTabCont==0,'leftList':true}"><span>上一月</span></li>
       <li @click="activeTabCont=1" :class="{active:activeTabCont==1,'centerList':true}"><span class="activeName">2019-03</span></li>
       <li @click="activeTabCont=2" :class="{active:activeTabCont==2,'rightList':true}"><span>下一月</span></li>
     </ul>
     <div class="bott omCont">
       <div v-show="activeTabCont==0" class="boxBorder">
         <ul >
           <li v-for="(shouyi,index) in shouyis" :key="index">
             <div class="leftImg"><img src="./../assets/images/game90.png" alt=""></div>
             <div class="centerBox">
               <h4>收益：+{{shouyi.shouyi}}</h4>
               <p>{{shouyi.text}}</p>
             </div>
             <div class="rightBox">
               <h4>余点：+{{shouyi.yudian}}</h4>
               <p>{{shouyi.titme}}</p>
             </div>
           </li>
         </ul>
       </div>
       <div v-show="activeTabCont==1" class="boxBorder">
         <ul >
           <li v-for="(shouyi,index) in shouyis" :key="index">
             <div class="leftImg"><img src="./../assets/images/game90.png" alt=""></div>
             <div class="centerBox">
               <h4>收益：+{{shouyi.shouyi}}</h4>
               <p>{{shouyi.text}}</p>
             </div>
             <div class="rightBox">
               <h4>余点：+{{shouyi.yudian}}</h4>
               <p>{{shouyi.titme}}</p>
             </div>
           </li>
         </ul>
       </div>
       <div v-show="activeTabCont==2" class="boxBorder">
         <ul >
           <li v-for="(shouyi,index) in shouyis" :key="index">
             <div class="leftImg"><img src="./../assets/images/game90.png" alt=""></div>
             <div class="centerBox">
               <h4>收益：+{{shouyi.shouyi}}</h4>
               <p>{{shouyi.text}}</p>
             </div>
             <div class="rightBox">
               <h4>余点：+{{shouyi.yudian}}</h4>
               <p>{{shouyi.titme}}</p>
             </div>
           </li>
         </ul>
       </div>
     </div>
     <!-- 底部导航菜单 -->
     <tabs :selected="selected"></tabs>
   </div>
</template>

<script>
import tabs from './../components/tabs'
import user from './../components/user'
export default {
  name: 'myProfit',
  data () {
    return {
      selected: '', // 导航当前页的状态
      bg: require('./../assets/images/game21.png'), // 背景图片
      activeTabCont: 0, // 游戏tab切换
      shouyis: [
        {
          shouyi: 0.50,
          text: '03.01下级流水167.3提成3%',
          yudian: 2.26,
          titme: '03.02 08:56:01'
        },
        {
          shouyi: 0.80,
          text: '04.23下级流水234提成5%',
          yudian: 45,
          titme: '04.23 11:56:01'
        }
      ],
      QR: require('../assets/images/game4.png'), // 用户头像
      money: 1.45, // 用户金额
      id: 353, // 用户id
      yuefen: '2019年03月'
    }
  },
  components: {
    tabs, user
  }
}
</script>

<style scoped>
  #myProfit{background-size: 100% 100%;height: 100%;position: fixed;width: 100%;}
  .tabTilte{display: inline-block;margin-top: 1rem; width: 80%; border-radius: 6px; background: rgba(94, 34, 36, 1);margin-left:10%;border: 1px solid #f44360;}
  .tabTilte li{float: left; overflow: hidden; width: 33.3%; text-align: center; color: #eaabab;height: 2rem; line-height: 2rem; }
  .boxBorder{ height:auto;display:block; width: 100%;position:relative; z-index: -1;}
  .boxBorder ul{display: inline-block; width: 100%;}
  .boxBorder ul li{display: inline-block; width: 100%; padding: 0.5rem 0;border-bottom: 1px solid #f44360;}
  .activeName{display:inline-block; width:100%; background:url("./../assets/images/game89.png") no-repeat; background-size: 101% 101%; color: #fff; font-weight: 600;}
  .boxBorder ul li:nth-child(1){ margin-top: 1rem;}
  .boxBorder ul li .leftImg{float: left;margin:0 0.8rem;}
  .boxBorder ul li .leftImg img{width: 2.5rem;}
  .centerBox{float: left;}
  .centerBox h4{color: #ffdd68; font-size: 1rem;}
  .centerBox p{color: #fff; font-size: 0.8rem;padding-top: 0.3rem;}
  .rightBox{float: right; padding-right: 0.8rem; text-align: right; color: #fff; }
  .rightBox h4{font-weight: 400;}
  .rightBox p{padding-top: 0.3rem;font-size: 0.8rem;}

  /*头部样式*/
  .homeHeader{width: 70%;height: 6rem;background: url("./../assets/images/game2.png") no-repeat;background-size: 100% 100%;float: left;}
  .headerLeft{width: 2.8rem; height: 2.8rem; text-align: center; float: left;margin-top:1.5rem; margin-left: 6%; background: url("./../assets/images/game3.png") no-repeat;background-size: 100% 100%;}
  .userImg{width: 100%; height: 100%;border-radius: 6px;border: 1.5px solid #fff;}
  .headerCenter{float: left;height: 4.5rem;margin-left: 0.6rem;margin-top: 1.4rem; font-size: 14px;}
  .userTitle{margin: 0.2rem 0;color: #fff;font-size: 0.8rem;}
  .userMoney{color: #fff;font-size: 0.6rem;}

  @media only screen and (max-width: 320px){
    .homeHeader {
      height: 7rem;
    }
    .headerLeft{
      width: 3.6rem;
      height: 3.6rem;
      margin-right: 0.3rem;
    }
  }

</style>



